<style>
	body {
		margin: 0;
	}

	.bg-mine {
		width: 100%;
		height: 180px;
		background: green;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		background-image: linear-gradient(to bottom right, #e4393c, #ecd533)
	}

	.bg-mine .bg-mine-portrait {
		width: 80px;
		height: 80px;
		overflow: hidden;
		margin: 0;
		padding: 0;
		border-radius: 50%
	}

	.bg-mine .bg-mine-portrait image {
		width: 80px;
		height: 80px;
		border-radius: 50%;
		object-fit: cover
	}

	.bg-mine .bg-mine-name {
		font-size: 14px;
		color: #fff;
		line-height: 30px;
		height: 30px
	}

	.weui-grid {
		position: relative;
		float: left;
		padding: 10px 5px;
		width: 50%;
		box-sizing: border-box;
		text-decoration: none;
	}

	.weui-grid .weui-grid__label {
		margin-top: 5px;
		display: block;
		text-align: center;
		color: #666;
		font-size: 14px;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden
	}

	.weui-grid:before {
		content: " ";
		position: absolute;
		right: 0;
		top: 0;
		width: 1px;
		bottom: 0;
		border-right: 1px solid #D9D9D9;
		color: #D9D9D9;
		-webkit-transform-origin: 100% 0;
		transform-origin: 100% 0;
		-webkit-transform: scaleX(0.5);
		transform: scaleX(0.5)
	}

	.weui-grid:after {
		content: " ";
		position: absolute;
		left: 0;
		bottom: 0;
		right: 0;
		height: 1px;
		border-bottom: 1px solid #D9D9D9;
		color: #D9D9D9;
		-webkit-transform-origin: 0 100%;
		transform-origin: 0 100%;
		-webkit-transform: scaleY(0.5);
		transform: scaleY(0.5)
	}

	.weui-grids {
		position: relative;
		overflow: hidden;
		background-color: #fff
	}

	.weui-grids:before {
		content: " ";
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		height: 1px;
		border-top: 1px solid #D9D9D9;
		color: #D9D9D9;
		-webkit-transform-origin: 0 0;
		transform-origin: 0 0;
		-webkit-transform: scaleY(0.5);
		transform: scaleY(0.5)
	}

	.weui-grids:after {
		content: " ";
		position: absolute;
		left: 0;
		top: 0;
		width: 1px;
		bottom: 0;
		border-left: 1px solid #D9D9D9;
		color: #D9D9D9;
		-webkit-transform-origin: 0 0;
		transform-origin: 0 0;
		-webkit-transform: scaleX(0.5);
		transform: scaleX(0.5);
	}


	.weui-grid__icon {
		width: 28px;
		height: 28px;
		margin: 0 auto;
		overflow: hidden
	}

	.weui-grid__icon image {
